body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgba(0, 10, 31, 255);
  /* overflow-y: scroll; */
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.link{
  cursor: pointer;
}

.navbar.colorChange{
  box-shadow: #00C7FF;
}

.navbar{
  box-shadow: none;
}

@media all and (min-width:651px) and (max-width:1024px){
  #whole-item{
    margin-left: 0px;
    
  }
}
@media all and (min-width:448px) and (max-width:650px){
  #whole-item{
    margin-left: 10px;
    
  }
}
@media all and (min-width:240px) and (max-width:447px){
  #whole-item{
    margin-left: 30px;
    
  }
}